import { z } from "zod";

const addNumbersSchema = z.object({
  a: z.number(),
  b: z.number(),
});

export const tools = {
  addNumbers: {
    description: "Add two numbers together",
    inputSchema: addNumbersSchema,
    execute: async ({ a, b }: { a: number; b: number }) => {
      return { result: a + b };
    },
  },
};

